<template>
	<view class="box" v-if="good_detail.promptPrice">
		<view class="goods-box">
			<swiper class="goods-box" :indicator-dots="true" :autoplay="true">
				<template v-for="(item,k) in good_detail.o2oChgPicList">
					<swiper-item class="swiper-item" :key="k">
						<image lazy-load class="img" :src="item.url" mode="aspectFill" :data-index="k" @tap="previewPhoto"></image>
					</swiper-item>
				</template>
			</swiper>

		</view>
		<view class="title-box">
			<view class="price">
				<view class="number">
					￥{{good_detail.promptPrice | formatNum}}
				</view>
				<view class="tips-box">
					<view class="bg-btn">分享可得￥{{good_detail.distributeRate1 | formatNum}}</view>
					<view class="border-btn">送{{good_detail.o2oMarketingRetCoupon.couponName}}</view>
				</view>
			</view>
			<view class="title">
				<view class="text">{{good_detail.spuTitle}}</view>
				<button open-type="share" class="btn-share iconfont iconfenxiang"></button>
			</view>

		</view>
		<view class="express-box">
			<view>
				<view class="title">
					快递
				</view>
				<view class="price" v-if="good_detail.logisticPayType=='CUSTOMER'">
					￥{{good_detail.logisticFee | formatNum}}
				</view>
				<view class="price" v-else>
					免运费
				</view>
			</view>

			<view class="sell-num" style="display: none;">销售量2001</view>
		</view>

		<!-- 产品详情 -->
		<view class="good-intro">
			<view class="tab-li active">产品详情</view>
			<!-- <view class="tab-li active">商品评价</view> -->
		</view>
		<jyf-parser class="good-detail" :html="good_detail.o2oBigText.content" lazy-load ref="article" selectable
		 show-with-animation use-anchor>加载中...</jyf-parser>
		<!-- 评价  暂时用不上 -->

		<view class="blank"></view>
		<view class="fix-box">
			<button open-type="contact" class="fix-btn no-btn">
				<view class="iconfont iconkefu"></view>
				<view class="text">
					客服
				</view>
			</button>

			<view class="fix-btn">
				<navigator url="/pages/goods/shop_car" class="iconfont icongouwuche1">
					<view class="good-num" v-if="cart_num!==0">{{cart_num}}</view>
				</navigator>
				<view class="text">
					购物车
				</view>
			</view>
			<view class="border-btn" @tap="intoCart">加入购物车</view>
			<view class="bg-btn" @tap="introOrder">立即购买</view>
			<!-- #ifdef MP -->
			<button v-if="!is_login" class="confirm-btn" open-type="getUserInfo" @getuserinfo="wxLogin">微信授权登录</button>
			<!-- #endif -->
		</view>
		<add-cart @specChoose='chooseType' @cartClose="closeCart" @cartReload='loadCartNum' v-if="cart_show" :showType="pup_type"
		 :specList.sync="good_detail.o2oSpuSpecList" :spuSpecSkuMap.sync="good_detail.o2oSpuSpecSkuMap" :goodDetail.sync="good_detail"></add-cart>
	</view>
</template>

<script>
	import addCart from './components/add-cart.vue'
	import score from '../../components/score.vue'
	import Vue from 'vue'
	import jwt_decode from "jwt-decode";
	import jyfParser from '@/components/jyf-parser/jyf-parser';
	import {mapState} from 'vuex'
	import {
		getProvider,
		getCode
	} from '@/common/js/login/newlogin.js'
	let provider = "";
	let seqId = "";
	let merchantShopId = "";
	let shareCode = "" //分享商品给我的人的邀请码
	export default {
		data() {
			return {
				is_login: false,
				cart_show: false,
				pup_type: "shopcar", //弹窗类型 
				good_detail: {
					o2oBigText: {
						content: ""
					},
					o2oChgPicList: [],
					o2oSpuSpecList: [],
					o2oSpuSpecSkuMap: [],
					o2oMarketingRetCoupon: {
						couponValue: ""
					}
				},
				cart_num: 0

			}
		},
		components: {
			addCart,
			score,
			jyfParser
		},
		computed: {
			...mapState({
				user: state => state.user
			})
		},
		onShow() {
			this.loadCartNum()
			this.cart_show = false;
			// #ifdef MP
			(async () => {
				//获取服务商信息
				provider = await getProvider();
				//获取code
				this.code = await getCode(provider[0])
			})()
			// #endif
		},
		onLoad(option) {
			seqId = option.seqId;
			merchantShopId = option.merchantShopId;
			if (shareCode) {
				// 分享链接进来
				this.$common.setLocal('good_shareCode', shareCode)
			} else {
				// 去其他页面之后返回到商品页
				shareCode = this.$common.getLocalSync('good_shareCode')
			}
			this.loadGoodDetail();
			let local_token = this.$common.getLocalSync('token');
			this.is_login = local_token ? true : false;
			uni.showLoading({
				title: "加载中"
			})
			setTimeout(function() {
				uni.hideLoading()
			}, 500)
		},
		// #ifdef MP
		onShareAppMessage(res) {
			return {
				title: this.good_detail.spuTitle,
				path: '/pages/goods/new_people_detail?seqId=' + seqId + '&merchantShopId=' + merchantShopId + '&shareCode=' + this.user.shareCode
			}
		},
		// #endif
		methods: {
			chooseType(e) {
				let key = e.key;
				let index = e.index;
				this.good_detail.o2oSpuSpecList[key].spec_choose = index
			},
			loadGoodDetail() {
				this.$api.get('/o2oSpu/getAllInfoByPkAndshopSeqId', {
					params: {
						seqId: seqId,
						merchantShopId: merchantShopId
					}
				}).then(res => {
					if (res.code == 200) {
						let data = res.data
						for (let item of data.o2oSpuSpecList) {
							// 默认选中第一个
							Vue.set(item, 'spec_choose', 0)
						}
						if (data.spuLabel) {
							// 转成数组
							data.spuLabel = data.spuLabel.split(',')
						}
						this.good_detail = data;
					}
				})
			},
			introOrder() {
				this.pup_type = 'buy';
				this.cart_show = true
			},
			intoCart() {
				this.pup_type = 'shopcar'
				this.cart_show = true
			},
			closeCart() {
				this.cart_show = false
			},
			// 微信登录
			wxLogin(e) {
				// #ifndef H5
				let that = this;
				if (!this.code) {
					uni.showToast({
						icon: 'none',
						title: '正在加载中，稍等一下',
						duration: 1000
					});
				}
				if (e.detail && e.detail.errMsg == 'getUserInfo:ok') {
					uni.getUserInfo({
						success(info_res) {
							that.info_res = info_res;
							that.sendLogin()
						}
					})
				}
				// #endif
			},
			sendLogin() {
				this.$api.post('/login/save', {
					code: this.code,
					rawData: this.info_res.rawData,
					iv: this.info_res.iv,
					signature: this.info_res.signature,
					encryptedData: this.info_res.encryptedData
				}).then(res => {
					if (res.code == 200) {
						let token = res.data;
						let user_json = jwt_decode(token);
						this.$common.setLocal('token', res.data);
						this.$common.setLocal('customerSeqId', user_json.userId);
						this.$common.setLocal('customerName', user_json.userName || "");
						this.$store.commit('saveUser', user_json)
						this.is_login = true;
					}
				})
			},
			// 获取购物车数量
			loadCartNum() {
				let customerSeqId = this.$common.getLocalSync('customerSeqId');
				if (!customerSeqId) return;
				this.$api.get('/o2oShopingCart/getEffectiveGoodsNum', {
					params: {
						customerSeqId: this.$common.getLocalSync('customerSeqId')
					}
				}).then(res => {
					if (res.code == 200) {
						if (res.data > 99) {
							this.cart_num = '99+';
						} else {
							this.cart_num = res.data;
						}
					}
				})
			},
			previewPhoto(e) {
				let index = e.currentTarget.dataset.index;
				let list = this.good_detail.o2oChgPicList.map((item, index) => {
					return item.url;
				});
				uni.previewImage({
					current: list[index],
					urls: list,
					longPressActions: {
						itemList: ['保存图片'],
						success: function(res) {
							uni.getImageInfo({
								src: list[res.index],
								success: function(image) {
									uni.saveImageToPhotosAlbum({
										filePath: image.path,
										success: function() {
											uni.showToast({
												title: '图片保存成功',
												icon: 'none',
												duration: 3000
											});
										}
									});
								}
							});
						}
					}
				});
			},
		}
	}
</script>

<style lang="scss">
	.primary-btn {
		margin: 0 20rpx;
	}

	.goods-box {
		width: 100%;
		height: 660rpx;

		.img {
			width: 100%;
			height: 100%;
		}
	}

	.title-box {
		width: calc(100% - 60rpx);
		padding: 30rpx;
		background: white;

		.price {
			width: 100%;
			height: auto;
			display: flex;
			align-items: center;

			.number {
				color: #FFA098;
				font-size: 44rpx;
				font-weight: bold;

				.ex_money {
					font-size: 24rpx;
					text-decoration: line-through;
					color: #a5a2a2;
					font-weight: normal;
					margin: 0 20rpx;
				}
			}

			.tips {
				color: #FD8C6B;
				border: 1rpx solid rgba(253, 140, 107, 1);
				border-radius: 6rpx;
				font-size: 20rpx;
				padding: 8upx 10upx;
				line-height: 1;
				margin-left: 8rpx;
				margin-top: 4rpx;
			}

			.tips-box {
				display: flex;

				.bg-btn {
					padding: 6rpx 20rpx;
					background: rgba(255, 228, 225, 1);
					border-radius: 22rpx;
					color: #ED6458;
					font-size: 22rpx;
					margin-left: 30rpx;
					font-weight: 500;
				}

				.border-btn {
					padding: 6rpx 20rpx;
					border: 1px solid rgba(255, 209, 205, 1);
					border-radius: 22rpx;
					color: #ED6458;
					font-size: 22rpx;
					margin-left: 30rpx;
					font-weight: 500;
				}
			}
		}

		.title {
			margin: 20rpx 0;
			font-size: 34rpx;
			font-weight: 500;
			line-height: 42rpx;
			display: flex;
			align-items: center;

			.text {
				width: 600rpx;
			}

			.btn-share::after {
				border: none;

			}

			.btn-share {
				background: white;
				font-size: 34rpx;
				line-height: 1;
			}
		}
	}

	.express-box {
		width: calc(100% - 60rpx);
		height: 100rpx;
		line-height: 100rpx;
		padding: 0 30rpx;
		display: flex;
		justify-content: space-between;
		font-size: 26rpx;
		font-weight: bold;
		background: white;
		margin: 30rpx 0;

		.title {
			margin-right: 70rpx;
			float: left;
		}

		.price {
			float: left;
		}

		.sell-num {
			color: #666666;
			font-weight: 500;
		}
	}

	.shop-box {
		width: calc(100% - 60rpx);
		height: 140rpx;
		background: white;
		padding: 0 30rpx;
		display: flex;
		justify-contenrt: space-between;
		align-items: center;

		.show-icon {
			width: 88rpx;
			height: 88rpx;
			background: #E6E6E6;
			border-radius: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			flex-shrink: 0;

			.img {
				width: 50rpx;
				height: 50rpx;
			}
		}

		.name-box {
			width: 400rpx;
			flex-shrink: 0;
			display: flex;
			align-content: center;
			flex-wrap: wrap;
			margin-left: 24rpx;

			.name {
				width: 100%;
				font-size: 30rpx;
				line-height: 50rpx;
			}

			.score-box {
				display: flex;

				.score {
					display: flex;
					margin-right: 20rpx;
				}

				.iconxingxing.active {
					color: #FFA098;
				}

				.point {
					font-size: 20rpx;
					font-weight: 500;
					color: #444444;
				}
			}
		}

		.goto {
			flex-grow: 1;
			font-size: 26rpx;
			font-weight: bold;
			display: flex;
			justify-content: flex-end;
			align-items: center;

			.iconfont {
				font-size: 26rpx;
				margin-left: 20rpx;
			}
		}
	}

	.good-intro {
		display: flex;
		margin: 30rpx;

		.tab-li {
			margin-right: 30rpx;
			font-size: 20rpx;
			font-weight: 500;
			position: relative;
			z-index: 2;
		}

		.active {
			font-size: 26rpx;
			font-weight: bold;

			&::after {
				content: "";
				position: absolute;
				width: 90rpx;
				height: 12rpx;
				background: rgba(255, 160, 152, 1);
				border-radius: 6rpx;
				left: 10rpx;
				bottom: 0;
				z-index: -1;

			}
		}
	}

	.good-detail {
		margin: 34upx;
		width: calc(100% - 108rpx);
		background: white;
		border-radius: 14rpx;
		font-size: 30rpx;
		padding: 20rpx;

		image {
			width: 100%;
		}
	}

	.good-comment {
		margin: 34upx;
		width: calc(100% - 68rpx);
		background: white;
		border-radius: 12rpx;

		.comment-li {
			padding: 30rpx;

			.name-box {
				display: flex;
				justify-content: space-between;
				align-items: center;

				.left {
					display: flex;
					align-items: center;
				}

				.img {
					width: 66rpx;
					height: 66rpx;
					border-radius: 100%;
					margin-right: 16rpx;
					background: $color-primary;

				}

				.name {
					font-size: 26rpx;
					font-weight: bold;
				}

				.time {
					font-size: 20rpx;
					color: #999999;
				}
			}

			.comment-text {
				font-size: 20rpx;
				overflow: hidden;
				text-overflow: ellipsis;
				display: -webkit-box;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
			}
		}

	}

	.fix-box {
		justify-content: space-between;

		.fix-btn {
			text-align: center;
		}

		.text {
			font-size: 20rpx;
		}

		.confirm-btn {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: red;
			z-index: 10;
			opacity: 0;
		}

		.iconfont {
			font-size: 40rpx;
			position: relative;

			.good-num {
				position: absolute;
				width: 24rpx;
				height: 24rpx;
				line-height: 24rpx;
				text-align: center;
				top: -10rpx;
				border: 1px solid rgba(250, 104, 92, 1);
				border-radius: 50%;
				right: 0;
				z-index: 1;
				font-size: 14rpx;
				font-weight: 500;
				color: rgba(250, 104, 92, 1);
				background: white;
			}
		}
	}
</style>
